const PTF = [
    { id: 1, title: " ", dec: "2022 8 17~1014", link: "/a" },
    { id: 2, title: "2  112 ", dec: "' 112 '\n .", link: "/a" },
    { id: 3, title: " , ", dec: "   \n", link: "/a" },
    { id: 4, title: "", dec: "    \n", link: "/a" },
]


const Portfolio = () => {

    return (
        <section className="Portfolio pfp" >
            <h2></h2>

            <div className="inner">
                {
                    PTF.map(content => {
                        return (
                            <figure key={content.id}>
                                <div className="box">
                                    <figure>
                                        <img src={process.env.PUBLIC_URL + "/assets/images/port1_" + content.id + ".gif"} alt="" />
                                    </figure>

                                    <div className="tit">{content.title}</div>
                                    <div className="dec">{content.dec}</div>
                                    <a href={content.link} className="cbtn"></a>
                                </div>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}

export default Portfolio;